<template>
  <div class="login">
    <van-nav-bar
      title="会员登陆"
      left-arrow
      @click-left="$router.go(-1)"
    ></van-nav-bar>

    <div class="container">
      <h2>手机号登录</h2>
      <p>未注册的手机号登录后将自动注册</p>
      <ul>
        <li>
          <input v-model="mobile" type="tel" placeholder="请输入手机号码" />
        </li>
        <li>
          <input
            v-model="captchaCode"
            type="text"
            placeholder="请输入图形验证码"
          /><img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="" />
        </li>
        <li class="last">
          <input
            v-model="smsCode"
            type="text"
            placeholder="请输入短信验证码"
          /><button @click="getCode">
            {{ scont === totalScont ? '获取验证码' : scont + '秒后重新发送' }}
          </button>
        </li>
      </ul>
      <div class="logon">
        <button @click="login">登录</button>
      </div>
    </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, codeLogin } from '@/api/picColde'
export default {
  name: 'LoginIndex',
  data() {
    return {
      picUrl: '',
      pickey: '',
      totalScont: 60,
      scont: 60,
      timer: null,
      mobile: '',
      captchaKey: '',
      captchaCode: '',
      smsCode: ''
    }
  },
  // 离开页面销毁定时器
  destroyed() {
    clearInterval(this.timer)
  },
  async created() {
    this.getPicCode()
  },
  methods: {
    // 获取图形验证码
    async getPicCode() {
      const {
        data: { base64, key }
      } = await getPicCode()
      this.picUrl = base64
      this.pickey = key
    },
    // 判断手机号和图形验证码
    validFn() {
      if (!/^1[2-9]\d{9}$/.test(this.mobile)) {
        this.$toast('手机号有误')
        return false
      }
      if (!/^\w{4}$/.test(this.captchaCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    // 获取短信验证码
    async getCode() {
      if (!this.validFn()) {
        return
      }
      await getMsgCode(this.captchaCode, this.pickey, this.mobile)
      // console.log(res)
      // if (res.status === 500) {
      //   this.$toast('图形验证码错误')
      //   this.captchaCode = ''
      //   this.getPicCode()
      //   return
      // }
      this.$toast('验证码已发送请注意查收')
      // 开启定时器
      if (!this.timer && this.scont === this.totalScont) {
        this.timer = setInterval(() => {
          this.scont--
          if (this.scont <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.scont = this.totalScont
          }
        }, 1000)
      }
    },
    // 登录
    async login() {
      if (!this.validFn()) {
        return this.$toast('手机号或效验码错误')
      }
      if (!/^\d{6}$/.test(this.smsCode) && this.smsCode === '') {
        return this.$toast('验证码错误')
      }
      const res = await codeLogin(this.mobile, this.smsCode)
      this.$store.commit('user/setUserInfo', res.data)
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
      this.$toast('登录成功')
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 40px;
  p {
    color: #aaa;
  }
  h2 {
    margin-bottom: 10px;
  }
  ul {
    padding: 20px 0 50px 0;
    li {
      margin-top: 10px;
      padding: 20px 0;
      border-bottom: 1px solid #ddd;
      input {
        border: 0;
      }
      img {
        width: 104px;
        margin-bottom: -15px;
      }
    }

    .last {
      button {
        border: 0;
        background-color: #fff;
        color: #f5a21c;
      }
    }
  }
  .logon {
    button {
      width: 100%;
      height: 40px;
      color: #fff;
      background-color: #f5a21c;
      border: 0;
      border-radius: 20px;
    }
  }
}
</style>
